.main-chart {
  height: 650px;
  min-height: 350px;
  width: 70%;
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 1300px) {
  .main-chart {
    width: stretch;
    margin-left: 15px;
    margin-right: 15px;
  }
}

@media screen and (max-width: 400px) {
  .main-chart {
    width: stretch;
    margin-left: 5px;
    margin-right: 5px;
  }
}

@media screen and (max-height: 450px) {
  .main-chart {
    height: 80vh;
    margin-left: 5px;
    margin-right: 5px;
  }
}